import React, { Component } from "react";
import "../styles/About.css";
import DotsNav from "./DotsNav";

import logo1 from "../images/logos/html.png";
import logo2 from "../images/logos/css.png";
import logo3 from "../images/logos/js.png";
import logo4 from "../images/logos/react.png";
import logo5 from "../images/logos/git.png";

class About extends Component {
  state = { activeDiv: 0 };

  changeActiveDiv = index => {
    if (index >= 0 && index < 2) {
      this.setState({
        activeDiv: index
      });
    } else return new Error("liczba kropek =/= liczba divow");
  };

  render() {
    const chooseContent = () => {
      if (this.state.activeDiv === 0)
        return (
          <div className="left">
            <p>
              Mam 22 lata. Studiuj na wydziale Automatyki, Elektroniki i
              Informatyki na Politechnic lskiej w Gliwicach. <br />
              <br />
              Programuj od wczesnych lat mojej edukacji. Zaczo si jzyka
              C++. Ju w Liceum wyrniaem si umiejtnociami. Na studiach
              wzmocniem swoj zdolnoci w myleniu algorytmicznym, nabraem
              praktyki dobrego programowania, mylenia i przewidywania. <br />
              <br />
              Jaki czas temu "odkryem" front-end, poczuem e to jest to, e
              chc si w tym rozwija. Jako samouk, tworzeniem stron
              internetowych nauczyem samemu, ogldajc kursy oraz cwiczc na
              wielu, maych projektach. <br /> <br />
              Programuj z pasji ! :)
            </p>
            <div className="hobbies">
              <p>Moje zainteresowania:</p>
              <div>
                <p>wiczy siowo</p>
              </div>
              <div>
                <p>Gra w siatkwk</p>
              </div>
              <div>
                <p>Tworzy aplikacje webowe</p>
              </div>
              <div>
                <p>Gotowa i je</p>
              </div>
            </div>
          </div>
        );
      else if (this.state.activeDiv === 1)
        return (
          <div className="right">
            <div className="logos">
              <img rel="preload" src={logo1} alt="html logo" />
              <img rel="preload" src={logo2} alt="css logo" />
              <img rel="preload" src={logo3} alt="js logo" />
              <img rel="preload" src={logo5} alt="git logo" />
              <img rel="preload" src={logo4} alt="react logo" />
            </div>
            <div className="container">
              <p>
                Wszystkie poniej wymienione technologie, jzyki, biblioteki
                ktre do tej pory poznaem, znam w najnowszych standardach na
                wysokim poziomie. Pisz kod przejrzysty i zrozumiay.
              </p>
              <ul className="technologies">
                <li>HTML ( and HTML5 elements )
                </li>
                <li>
                  CSS3 ( flex-box, responsive web design (media-queries),
                  scss/sass, animations (key-frames) )
                </li>
                <li>
                  JavaScript ECMAScript6 ( jQuery basics, Object Oriented
                  Programming)
                </li>
                <li>React ( React Router, fetch API )</li>
                <li>Git ( Github )</li>
              </ul>
            </div>
          </div>

        );
    };
    const content = chooseContent();

    return (
      <div className="about">
        <h1>O mnie</h1>
        <section className="container">{content}</section>
        <DotsNav
          numberOfDots={2}
          changeActiveDiv={this.changeActiveDiv}
          activeDot={this.state.activeDiv}
        />
      </div>
    );
  }
}

export default About;
